import { Component, OnInit } from '@angular/core';
import { ViewEncapsulation } from '@angular/core';
import { FormsModule }   from '@angular/forms';
import { Router, NavigationEnd } from '@angular/router';
import { HomepageService } from './homepage.service';

@Component({
  selector: 'app-homepage',
  templateUrl: './homepage.component.html',
  styleUrls: ['./homepage.component.css'],
 encapsulation: ViewEncapsulation.None,
  providers: [HomepageService]
})
export class HomepageComponent implements OnInit {
public _timeTaken: number = 0;
public oldTime: number = 0;
public _vehicles = [];
public _planets = [];
public _planets2 = [];
public _planets3 = [];
public _planets4 = [];
public second_option: boolean = true;
public third_option: boolean = true;
public fourth_option: boolean = true;
public showSpinner: boolean = false;
public oldValue: String = '';
public _token: any = '';



  constructor(private homeService: HomepageService, public router: Router) { }

  ngOnInit() {
    //api call for getting planets.
    this.planets();
    //api call for getting space ships.
    this.vehicles();
    //api call for getting token.
    this.token();
  }

public planets() {
  //api call for getting the planets.
  this.homeService.getPlanets().subscribe(
        (res) => {
	        this._planets = res;
      }, (err) => {
        
      });
}


public vehicles() {
  //api call for getting the space ships.
  this.homeService.getVehicles().subscribe(
        (res) => {
	        this._vehicles = res;
      }, (err) => {

      });
}


public token() {
  // handing the session data for token.
  let sessionData = sessionStorage.getItem("token");
    if ( sessionData !== null && sessionData.length !== 0) {
      let data = JSON.parse(sessionData);
      this._token = data.token;
    }else { //api call for token and storing the data in session storage.
      this.homeService.getToken().subscribe(
          (res) => {
          sessionStorage.setItem("token", JSON.stringify(res));
        }, (err) => {

        });
    } 
}


public findFalcone(firstOption, secondOption, thirdOption, fourthOption, orbit1, orbit2, orbit3, orbit4) {
  this.showSpinner = true; // for displaying spinner
  let planet_names = []; // declaring the planets array
  let vehicle_names = []; //declaring the vehicle_names array for space ships

  //creating a planet_names array for body of the request
  planet_names.push(orbit1);
  planet_names.push(orbit2);
  planet_names.push(orbit3);
  planet_names.push(orbit4);
  //creating a vehicle_names array for body of the request
  vehicle_names.push(firstOption);
  vehicle_names.push(secondOption);
  vehicle_names.push(thirdOption);
  vehicle_names.push(fourthOption);

 //api call
 this.homeService.getResult(this._token, planet_names, vehicle_names).subscribe(
        (res) => {
          // *if the status is false.
          if(res.status == 'false') {
            this.router.navigate(['/failure']);
          }else if(res.status == 'success') { //*if the status is success.
            this.router.navigate(['/success', {place:res.planet_name, time: this._timeTaken}]);
          }
        	this._token = res;
          this.showSpinner = false;
      }, (err) => {
        //*if the token is expired remove the token from the sessionStorage and recursive call.
        sessionStorage.removeItem("token");
        this.token(); //getting the new token after clearing the session Storage.
        this.findFalcone(firstOption, secondOption, thirdOption, fourthOption, orbit1, orbit2, orbit3, orbit4);
      });
}

public updateTime() {
  // this function can take n number of arguments as of now i used 8 arguments.
  this._timeTaken = 0;
   for (var i = 0; i < 4; i++) { //increment the loop upto index 3 (first four indexes contains the spaceships name)
     for(let j in this._vehicles) {
       if(this._vehicles[j].name === arguments[i]) {
         for(let k in this._planets) {
           if(this._planets[k].name == arguments[i+4]) { //next four indexes 4-7 contains the planet names.
               this._timeTaken += (this._planets[k].distance / this._vehicles[j].speed); // calculating the time taken.
           }
         }
       }
     }
    }
}

public updateRemainingUnits(newval) {
  /** saving the previous selected value and comparing the value to increment the old vehicle count **/
  for(let i in this._vehicles) {
    if(this.oldValue.length !== 0 && this.oldValue !== newval) {
     if(this._vehicles[i].name === this.oldValue) {
       this._vehicles[i].total_no = this._vehicles[i].total_no + 1;
       this.oldValue = '';
       break;
     }
    }
  }
  /** decrement the vehicle count by 1 **/
  for(let i in this._vehicles) {
    if(this._vehicles[i].name === newval) {
      this._vehicles[i].total_no = this._vehicles[i].total_no - 1;
      this.oldValue = newval;
      break;
    }
  }
}

public updateDestinationTwo(val) {
  //update the planet2 destination array
  this._planets2 = [];
  for(let i in this._planets) {
    if(this._planets[i].name !== val) {
        this._planets2.push(this._planets[i]);
    }
  }
}

public updateDestinationThree(val) {
  //update the planet2 destination array
  this._planets3 = [];
  for(let i in this._planets2) {
    if(this._planets2[i].name !== val) {
        this._planets3.push(this._planets2[i]);
    }
  }
}

public updateDestinationFour(val) {
  //update the planet2 destination array
  this._planets4 = [];
  for(let i in this._planets3) {
    if(this._planets3[i].name !== val) {
        this._planets4.push(this._planets3[i]);
    }
  }
}

public canReach(maxDistance, name) {
  //show or hide the spaceships based up on the max distance it can cover.
  for(let i in this._planets) {
    if(this._planets[i].name == name) {
      return (maxDistance < this._planets[i].distance);
    }
  }
}

}



