import { NgModule } from '@angular/core';

import {
  MatButtonModule,
  MatMenuModule,
  MatToolbarModule,
  MatIconModule,
  MatCardModule,
MatSelectModule,
MatRadioModule,
MatGridListModule,
MatProgressSpinnerModule
} from '@angular/material';



@NgModule({
  imports: [
    MatButtonModule,
    MatMenuModule,
    MatToolbarModule,
    MatIconModule,
    MatCardModule,
MatSelectModule,
MatRadioModule,
MatGridListModule,
MatProgressSpinnerModule
  ],
  exports: [
    MatButtonModule,
    MatMenuModule,
    MatToolbarModule,
    MatIconModule,
    MatCardModule,
MatSelectModule,
MatRadioModule,
MatGridListModule,
MatProgressSpinnerModule
  ]
})
export class MaterialModule {}



