import { Injectable } from '@angular/core';
import { Http, Headers, RequestOptions, RequestMethod, Request } from '@angular/http';
// Compiler complains if this isnt included.
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';

@Injectable()
export class HomepageService {

  constructor(private http: Http) { }

public getPlanets() {
    return this.http.get('https://findfalcone.herokuapp.com/planets').map(
        (res) => res.json()
    );
}

public getVehicles() {
    return this.http.get('https://findfalcone.herokuapp.com/vehicles').map(
        (res) => res.json()
    );
}

public getToken() {
let headers = new Headers({ 'Accept': 'application/json' });
let options = new RequestOptions({ headers: headers });
let body = {};

    return this.http.post('https://findfalcone.herokuapp.com/token', body ,options ).map(
        (res) => res.json()
    );
}

public getResult(token, planet_names, vehicle_names) {
let headers = new Headers({ 'Accept': 'application/json', 'Content-Type': 'application/json' });
let options = new RequestOptions({ headers: headers });

let body = {"token":token, planet_names, vehicle_names};

    return this.http.post('https://findfalcone.herokuapp.com/find', body ,options ).map(
        (res) => res.json()
    );
}

}



